import router from './router'
import store from './store'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

/**
 * is business path
 * @param path
 * @returns {boolean}
 */
const isBusinessPath = function (path) {
  if (!!path && (path.startsWith('/func/')
      || path.startsWith('/manage/')
      || path.startsWith('/system/'))) {
    return true;
  }
  return false;
}

/**
 * update page title
 */
const pageTitle = function (to) {
  if (to.meta.title) {
    document.title = to.meta.title;
  } else {
    document.title = '税务公社个税计算服务';
  }
}


router.beforeEach((to, from, next) => {
  NProgress.start()
  pageTitle(to)
  if (to.path === '/login' || to.path.startsWith('/register')) {
    if (!!store.getters.token) {
      next({path: '/'})
      NProgress.done()
    } else {
      next()
    }
  } else if (to.path === '/logout') {
    store.dispatch('LogOut').then(() => {
      next('/login')
      NProgress.done()
    })
  } else if (!!store.getters.token) {
    if (!store.getters.user.info) {
      store.dispatch('GetUser').then(() => {
        next({...to})
      });
    } else if (to.path.startsWith('/system') && store.getters.user.info.roles != 'admin') {
      next('/401')
    } else {
      next();
    }
  } else {
    var to = ''
    if (isBusinessPath(to.path)) {
      to = encodeURI(to.path)
    }
    next({
      path: '/login',
      query: {
        to: to
      }
    })
    NProgress.done()
  }
})

router.afterEach(() => {
  NProgress.done()
})