import fetch from '../fetch'

/**
 * login service
 */
const Login = {
  qrcode: () => fetch({
    url: '/api/login/qrcode',
    method: 'get'
  }),
  touch: (token) => fetch({
    url: '/api/login/touch',
    method: 'post',
    data: {token: token}
  }),
  doLogin: (token) => fetch({
    url: '/api/login/do',
    method: 'post',
    data: {token: token}
  }),
  doMock: (token) => fetch({
    url: '/api/login/mock',
    method: 'post',
    data: {token: token}
  }),
};

export default Login