import fetch from '../fetch'

/**
 * reqister service
 */
const Reqister = {
  qrcode: () => fetch({
    url: '/api/reqister/qrcode',
    method: 'get'
  }),
  touch: (token) => fetch({
    url: '/api/reqister/touch',
    method: 'post',
    data: {token: token}
  }),
  doReqister: (user, token) => fetch({
    url: '/api/reqister/do',
    method: 'post',
    data: {manager: user, token: token}
  }),
  doMock: (token) => fetch({
    url: '/api/reqister/mock',
    method: 'post',
    data: {token: token}
  }),
};

export default Reqister