import UserApi from '@/api/manage/user'
import LoginApi from '@/api/user/login'
import ReqisterApi from '@/api/user/reqister'
import {getToken, setToken, removeToken} from '@/api/auth'

const user = {
  state: {
    token: getToken(),
    user: {
      info: null,
    }
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
      setToken(token)
    },
    SET_USER: (state, user) => {
      state.user.info = user
    }
  },

  actions: {
    Login({commit}, token) {
      return new Promise((resolve, reject) => {
        LoginApi.doLogin(token).then(res => {
          if (res.success) {
            commit('SET_TOKEN', res.obj)
            resolve(res)
          }
        }).catch(error => {
          reject(error)
        })
      })
    },
    Reqister({commit}, data) {
      return new Promise((resolve, reject) => {
        ReqisterApi.doReqister(data.manager, data.token).then(res => {
          if (res.success) {
            commit('SET_TOKEN', res.obj)
          }
          resolve(res)
        }).catch(error => {
          reject(error)
        })
      })
    },
    GetUser({commit}, to) {
      return new Promise((resolve, reject) => {
        UserApi.get().then(res => {
          commit('SET_USER', res.obj)
          resolve(res)
        }).catch(error => {
          reject(error)
        })
      })
    },
    Logout({commit}) {
      return new Promise((resolve, reject) => {
        UserApi.logout().then(res => {
          commit('SET_USER', null)
          commit('SET_TOKEN', '')
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    }
  }
}

export default user
