import fetch from '../fetch'

/**
 * payitem service
 */
const User = {
  get: () => fetch({
    url: '/api/user/get',
    method: 'get'
  }),
  info: () => fetch({
    url: '/api/user/info',
    method: 'get'
  }),
  qrcode: () => fetch({
    url: '/api/user/manager/qrcode',
    method: 'get'
  }),
  touch: (token) => fetch({
    url: '/api/user/manager/touch',
    method: 'post',
    data: {token: token}
  }),
  managers: () => fetch({
    url: '/api/user/managers',
    method: 'get'
  }),
  saveUser: (data) => fetch({
    url: '/api/user/save/user',
    method: 'post',
    data: data
  }),
  saveApp: (data) => fetch({
    url: '/api/user/save/app',
    method: 'post',
    data: data
  }),
  saveArea: (area) => fetch({
    url: '/api/user/save/area',
    method: 'post',
    data: area
  }),
  delete: (id) => fetch({
    url: '/api/user/delete',
    method: 'post',
    data: {id: id}
  }),
  logout: () => fetch({
    url: '/api/logout',
    method: 'get'
  })
};

export default User